
.container{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: linear-gradient(#121212, #212B46
    );
}

.title{
    font-size: 85px;
    color: white;
    animation: fliptitle 2s;
}


.containerInput{
    display: flex;
    background-color: rgba(255, 255, 255, 0.2);
    padding: 15px;
    margin: 34px 0px;
    border-radius: 8px;
    box-shadow: 1px 3px 8px rgba(0,0,0, 0.5);
}

.containerInput input{
    background-color: transparent;
    border: 0px;
    font-size: 20px;
    color: white;
    outline: none;
    margin-right: 10px;
}

.containerInput input::placeholder{
    color: #F1F1F1;
}

.buttonSearch{
    background-color: transparent;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: 0.5s;
}

.buttonSearch:hover{
    transform: scale(1.2);

}

.main{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: white;
    width: 500px;
    border-radius: 10px;
}

.main h2{
    margin: 10px 0px;
    font-size: 28px;
}

.main span{
    margin-bottom: 10px;
    font-weight: bold;
}

@media (max-width: 720px){
    .title{
        font-size: 58px;
    }

    .mais h2{
        font-size: 28px;
    }

    .main{
        width: 80%;
        min-height: 150px;
    }
    
}

@keyframes fliptitle {
    
    from{
        transform: rotateX(90deg);
    }
    to{
        transform: rotateX(0deg);
    }
}

